<script>
import axios from "axios"
import PostSummary from "../components/PostSummary.vue"
export default {
	data() {
		return { posts: [] }
	},
	mounted() {
		const fetchArticles = async () => {
			const url = "http://localhost:4000/articles"
			try {
				const response = await axios.get(url)

				this.posts = response.data
			} catch (error) {
				console.error(error)
			}
		}
		fetchArticles()
	},

	components: { PostSummary },
}
</script>

<template>
	<div class="about">
		<div v-if="posts.length > 0">
			<PostSummary v-for="post in posts" :key="post.id" :post="post" />
		</div>
		<div v-else>
			No posts yet

			<br />
			<router-link to="/new-post">Add Post</router-link> |
		</div>
	</div>
</template>
